import React, { useState, useCallback, useRef } from 'react';
import Webcam from 'react-webcam';
//importacion de firebase
import * as firebase from 'firebase';

export default function WebcamComponent({
	UserName,
	Cont,
	nextQuestion,
	setFlagVideo,
}) {
	/* videoCapturing */
	const [capturing, setCapturing] = useState(false);
	const [recordedChunks, setRecordedChunks] = useState([]);
	const webcamRef = useRef(null);
	const mediaRecorderRef = useRef(null);

	/* firebase  */
	const storage = firebase.storage();
	const database = firebase.database();

	const handleDataAvailable = useCallback(
		({ data }) => {
			if (data.size > 0) {
				setRecordedChunks((prev) => prev.concat(data));
			}
		},
		[setRecordedChunks]
	);

	const handleStartCaptureClick = useCallback(() => {
		setCapturing(true);
		mediaRecorderRef.current = new MediaRecorder(
			webcamRef.current.stream,
			{
				mimeType: 'video/webm',
			}
		);
		mediaRecorderRef.current.addEventListener(
			'dataavailable',
			handleDataAvailable
		);
		mediaRecorderRef.current.start();
	}, [handleDataAvailable]);

	const handleStopCaptureClick = useCallback(() => {
		mediaRecorderRef.current.stop();
		setCapturing(false);
	}, [mediaRecorderRef]);

	const handleSend = useCallback(() => {
		const userName = UserName;
		console.log(userName);
		if (recordedChunks.length) {
			const blob = new Blob(recordedChunks, {
				type: 'video/webm',
			});

			/* send data to firebase */
			const storageRef = storage.ref(
				`/videos/${userName}/pregunta-${Cont}.webm`
			);
			storageRef.put(blob).then(function (snapshot) {
				snapshot.ref.getDownloadURL().then((downloadURL) => {
					console.log(downloadURL);
					database
						.ref(`users/${userName}/answers/${Cont}`)
						.update({ url: downloadURL });
					nextQuestion();
					setFlagVideo(false);
				});
				console.log('Uploaded a blob or file!');
			});

			/* end sed data to firebase */

			const url = URL.createObjectURL(blob);
			const a = document.createElement('a');
			document.body.appendChild(a);
			a.style = 'display: none';
			a.href = url;
			a.download = `video-${userName}-pregunta-${Cont}.webm`;
			a.click();
			window.URL.revokeObjectURL(url);
			setRecordedChunks([]);
		}
	}, [Cont, UserName, recordedChunks, storage]);
	return (
		<div>
			<Webcam
				className="embed-responsive mt"
				audio={true}
				ref={webcamRef}
				style={{ maxHeight: '35em' }}
			/>
			{capturing ? (
				<button
					className="btn btn-danger btn-lg btn-block m-2"
					onClick={handleStopCaptureClick}
				>
					Parar Grabación
				</button>
			) : (
				<button
					className="btn btn-success btn-lg btn-block m-2"
					onClick={handleStartCaptureClick}
				>
					Iniciar Grabación
				</button>
			)}
			{recordedChunks.length > 0 && (
				<button
					className="btn btn-primary btn-lg btn-block m-2"
					onClick={() => handleSend(UserName)}
				>
					Descargar y Enviar Video Respuesta
				</button>
			)}
		</div>
	);
}
