import React, { Suspense, useState, useEffect } from 'react';

import { Link, useHistory } from 'react-router-dom';

//import components
import { Canvas } from 'react-three-fiber';
import { Skydome } from '../../components/Sphere360/Sphere360';
import { CameraControls } from '../../components/Camera/Camera';
import Video from '../../components/Video/Video';
import ButtonsVideo from '../../components/ButtonsVideo/ButtonsVideo';
import FormQuestions from '../../components/FormQuestions/FormQuestions';
import Webcam from '../../components/Webcam/Webcam';

//importacion de firebase
import * as firebase from 'firebase';

function Home({ userState, setUserState }) {
	const [videoState, setVideoState] = useState({
		videoPlay: false,
		url: `${process.env.PUBLIC_URL}/Assets/videos/p6.mp4`,
	});
	const [FlagVideo, setFlagVideo] = useState(false);
	const [CountQuestion, setCountQuestion] = useState(6);
	const [Questions, setQuestions] = useState({
		1: { options: {}, video: false },
	});
	const [AnswerQuestions, setAnswerQuestions] = useState({});

	const db = firebase.database();
	const questionsRef = db.ref('questions');
	const answerQuestionsRef = db.ref('answer_questions');

	let history = useHistory();

	useEffect(() => {
		questionsRef.on('value', (snapshot) => {
			setQuestions(snapshot.toJSON());
		});
		answerQuestionsRef.on('value', (snapshot) => {
			setAnswerQuestions(snapshot.toJSON());
		});
	}, []);

	const nextQuestion = () => {
		setCountQuestion(CountQuestion + 1);
		db.ref(
			`users/${userState.uid}/answers/${CountQuestion}`
		).update({ option: 'a', rating: 0 });
	};

	const nextQuestionAnswer = async (answer = 'a') => {
		console.log(CountQuestion);
		if (CountQuestion !== Object.keys(AnswerQuestions).length) {
			setCountQuestion(CountQuestion + 1);
			const answerDB = AnswerQuestions[CountQuestion].answer;
			if (answerDB === answer) {
				db.ref(
					`users/${userState.uid}/answers/${CountQuestion}`
				).update({ option: 'a', rating: 1 });
			} else {
				db.ref(
					`users/${userState.uid}/answers/${CountQuestion}`
				).update({ option: 'a', rating: 0 });
			}
			setVideoState({
				...videoState,
				url: `${process.env.PUBLIC_URL}/Assets/videos/p${
					CountQuestion < Object.keys(Questions).length
						? CountQuestion + 1
						: CountQuestion
				}.mp4`,
			});
			setFlagVideo(false);
		} else {
			await setUserState({
				...userState,
				state: false,
				email: '',
			});
			firebase
				.auth()
				.signOut()
				.then(function () {
					// Sign-out successful.
					history.push('/');
				})
				.catch(function (error) {
					// An error happened.
				});
		}
	};

	return (
		<div className="container-fluid">
			<div className="row justify-content-center mt-4 mb-5">
				<div className="col-md-8">
					{FlagVideo ? (
						Questions[CountQuestion]?.video ? (
							<Webcam
								UserName={userState.uid}
								Cont={CountQuestion}
								nextQuestion={nextQuestion}
								setFlagVideo={setFlagVideo}
							/>
						) : (
							<div />
						)
					) : (
						<>
							<Canvas
								colorManagement
								style={{
									height: '500px',
									border: '3px solid black',
									width: '100%',
								}}
								camera={{ position: [0, 0, 0.1] }}
							>
								<CameraControls />
								<ambientLight intensity={0.2} />
								<spotLight
									position={[10, 10, 10]}
									angle={0.15}
									penumbra={1}
								/>
								<pointLight position={[-10, -10, -10]} />
								<Suspense fallback={null}>
									<Skydome></Skydome>
								</Suspense>
								<Video
									position={[0, 0, -3]}
									url={videoState.url}
									state={videoState}
									setState={setVideoState}
									setFlagVideo={setFlagVideo}
								></Video>
							</Canvas>
							<ButtonsVideo
								videoState={videoState}
								setVideoPlay={setVideoState}
								videoPlay={videoState.videoPlay}
							/>
						</>
					)}
				</div>
				{FlagVideo ? (
					<div className="col-md-6">
						<FormQuestions
							Questions={Questions}
							AnswerQuestions={AnswerQuestions}
							CountQuestion={CountQuestion}
							nextQuestion={nextQuestionAnswer}
						/>
					</div>
				) : (
					<div className=""></div>
				)}
			</div>
		</div>
	);
}

export default Home;
