import React, { useEffect, useState } from 'react';
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Redirect,
} from 'react-router-dom';

//importo firebase
import * as firebase from 'firebase';

//components
import ProtectedRoute from './components/ProtectedRoute/ProtectedRoute';
import Navbar from './components/Navbar/Navbar';

//views
import Login from './views/Login/Login';
import Home from './views/Home/Home';
import NotFound404 from './views/404/NotFound404';

function App() {
	const [userState, setUserState] = useState({
		state: false,
		uid: '',
		initializing: true,
		email: '',
	});

	useEffect(() => {
		firebase.auth().onAuthStateChanged((user) => {
			if (user) {
				// User is signed in.
				const uid = user.uid;
				const email = user.email;

				setUserState({
					...userState,
					uid,
					state: true,
					initializing: false,
					email,
				});
				// ...
			} else {
				// User is signed out.
				setUserState({
					...userState,
					uid: '',
					state: false,
					initializing: false,
					email: '',
				});
			}
		});
	}, [setUserState]);

	const SetStateRoute = (state) => {
		setUserState({
			...userState,
			initializing: state,
		});
	};

	if (userState.initializing) {
		return (
			<div className="container">
				<div className="row">
					<div className="col-12 vh-100 d-flex flex-column justify-content-center align-items-center">
						<div className="spinner-border" role="status">
							<span className="sr-only">Cargando...</span>
						</div>
						<p>Cargando</p>
					</div>
				</div>
			</div>
		);
	}

	return (
		<Router>
			<Navbar userMail={userState.email}></Navbar>
			<Switch>
				<ProtectedRoute
					exact
					path="/test"
					user={userState.state}
					component={Home}
					userState={userState}
					setUserState={setUserState}
				/>
				<Route
					exact
					path="/"
					render={(props) => {
						if (userState.email === '') {
							return <Login SetStateRoute={SetStateRoute} />;
						} else {
							return (
								<Redirect
									to={{
										pathname: '/test',
										state: {
											from: props.location,
										},
									}}
								/>
							);
						}
					}}
				></Route>
				<Route path="*">
					<NotFound404 />
				</Route>
			</Switch>
		</Router>
	);
}

export default App;
