import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
//importo firebase
import * as firebase from 'firebase';

export default function Login({ SetStateRoute }) {
	const [User, setUser] = useState({
		userName: '',
		password: '',
	});
	const history = useHistory();

	function handleSubmit(evt) {
		evt.preventDefault();
		login();
	}

	const login = () => {
		firebase
			.auth()
			.signInWithEmailAndPassword(User.userName, User.password)
			.then((resolve) => {
				SetStateRoute(true);
				history.push('/test');
			})
			.catch(function (error) {
				// Handle Errors here.
				alert('No se pudo iniciar sesion');
				// ...
			});
	};

	function handleChangeInput(e) {
		const inputName = e.target.name;
		const inputValue = e.target.value;
		inputName === 'userName'
			? setUser({ ...User, userName: inputValue })
			: setUser({ ...User, password: inputValue });
	}

	return (
		<div className="container-fluid vh-100">
			<div className="row justify-content-center h-100">
				<div className="col-md-6 d-flex align-items-center">
					<div
						className="card shadow w-100"
						style={{ height: '35em' }}
					>
						<div className="card-header d-flex flex-column align-items-center justify-content-center bg-white border-bottom-0">
							<img
								src={`${process.env.PUBLIC_URL}/Assets/images/kaleidolab.jpg`}
								alt=""
								width="300"
							/>
							<h3>Assessment VR</h3>
							<p className="text-center">
								La experiencia de realidad virtual para
								desarrollar y aplicar evaluaciones de
								comportamientos ante situaciones inesperadas
							</p>
						</div>
						<div className="card-body">
							<form onSubmit={handleSubmit}>
								<div className="form-group">
									<label htmlFor="usuario">Usuario</label>
									<input
										type="text"
										className="form-control"
										id="usuario"
										aria-describedby="emailHelp"
										name="userName"
										onChange={handleChangeInput}
									/>
								</div>
								<div className="form-group">
									<label htmlFor="Password">Contraseña</label>
									<input
										type="password"
										className="form-control"
										name="password"
										id="Password"
										onChange={handleChangeInput}
									/>
								</div>
								<button
									type="submit"
									className="btn btn-primary btn-block my-4"
								>
									Ingresar
								</button>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
