import React from 'react';

export default function FormQuestions({
	Questions,
	AnswerQuestions,
	CountQuestion,
	nextQuestion,
}) {
	return (
		<div className="container-fluid">
			<div className="row">
				<div className="col-md-12">
					<h5>{Questions[CountQuestion]?.question}</h5>
				</div>
			</div>
			<div className="row">
				<div className="col-md-12 d-flex flex-column">
					{Questions[CountQuestion]
						? Object.entries(
								Questions[CountQuestion]?.options
						  ).map(([key, value], i) => {
								return (
									<button
										name="dato1"
										className="btn btn-primary btn-block btn-lg"
										key={key + i}
										onClick={(e) => {
											nextQuestion();
										}}
									>
										{value}
									</button>
								);
						  })
						: ''}
				</div>
			</div>
		</div>
	);
}
