import React from 'react';

export default function ButtonsVideo({
	setVideoPlay,
	videoState,
	videoPlay,
}) {
	return (
		<>
			{videoPlay ? (
				<button
					type="button"
					className="btn btn-warning btn-block btn-lg mt-2"
					onClick={(e) => {
						setVideoPlay({
							...videoState,
							videoPlay: !videoState.videoPlay,
						});
					}}
				>
					Pausar
				</button>
			) : (
				<button
					type="button"
					className="btn btn-success btn-block btn-lg mt-2"
					onClick={(e) => {
						setVideoPlay({
							...videoState,
							videoPlay: !videoState.videoPlay,
						});
					}}
				>
					Iniciar
				</button>
			)}
		</>
	);
}
