import React from 'react';
import { TextureLoader, BackSide } from 'three';
import { useLoader } from 'react-three-fiber';

export const Skydome = ({ position }) => {
	const texture = useLoader(
		TextureLoader,
		`${process.env.PUBLIC_URL}/Assets/images/360_world_office.jpeg`
	);
	return (
		<mesh>
			<sphereBufferGeometry
				attach="geometry"
				args={[500, 60, 40]}
			/>
			<meshBasicMaterial
				attach="material"
				map={texture}
				side={BackSide}
			/>
		</mesh>
	);
};
